@charset "utf-8";

ul,ol,li{
  list-style: none;
}
#t_txt{
  margin: 0px auto;
  position: absolute;
  margin-bottom: 550px;
  left:22%;
}
#t_txt label{
  text-align: left;
}
.data_txt input{
  margin-left: 115px;
  background-color: rgb(88, 88, 255);
  color: #fff;
  border: none;
  padding: 5px 10px 5px 10px;
}
#t_txt .pro_txt input{
  margin-left: 53px;
}
#t_txt .price_txt input{
  margin-left: 38px;
}
.wrap{
  width: 500px;height: 1200px;
  padding: 10px; margin: 0px auto;
  border: 3px solid #000;
  position: relative;
  background-color: aquamarine;
  overflow: hidden;
}
.wrap h2{
  text-align: center;
 
}
.product_list{
  width: 48%;
  float: left;
  margin: 120px 10px 30px 0px;
  padding: 30px;
  transform: translateY(100px);
}
.product_list img{
  width:200px;
  position: absolute;
  left: 0;
}
.product_list input{
  position: absolute;
  right: 0%;
}
.product_list p{
  position: absolute;
  font-size: 12px;
  text-align: left;
  left:70%;
  width: 200px;
}
.pri_name{
  transform:translateY(120px)
}
.pro_name{
  transform: translateY(-10px);
}
.delete{
  transform: translate(70px,120px);
}